body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{color:#00B4FF;font-family:"Fira Sans", Sans-serif;font-size:16px;font-weight:500;line-height:1em;margin:0px 0px 9px 0px}body{font:normal 1rem "Fira Sans", Sans-serif}.company-title{color:#2E75B6;margin:0px 0px 2px 0px;padding:0px 0px 0px 0px;color:#150D43;font-family:"Fira Sans", Sans-serif;font-size:26px;font-weight:700}.company-title:hover{color:#00B4FF}.time{color:#00B4FF;font-family:'Fira Sans', sans-serif;font-weight:600}.experience{list-style:none}.img{height:100px;object-fit:cover}.timeline{border-left:3px solid #00B4FF;border-bottom-right-radius:4px;border-top-right-radius:4px;margin:0 8em;letter-spacing:0.2px;position:relative;line-height:1.4em;font-size:1.03em;padding:50px;list-style:none;text-align:left}@media (max-width: 767px){.timeline{max-width:98%;padding:25px}}.timeline h1{font-weight:300;font-size:1.4em}.timeline h2,.timeline h3{font-weight:600;font-size:1rem;margin-bottom:10px}.timeline .event{border-bottom:1px dashed #e8ebf1;padding-bottom:25px;margin-bottom:25px;position:relative}@media (max-width: 767px){.timeline .event{padding-top:30px}}.timeline .event:last-of-type{padding-bottom:0;margin-bottom:0;border:none}.timeline .event:before,.timeline .event:after{position:absolute;display:block;top:0}.timeline .event:before{left:-207px;content:attr(data-date);text-align:right;font-weight:100;font-size:0.9em;min-width:120px}@media (max-width: 767px){.timeline .event:before{left:0px;text-align:left}}.timeline .event:after{box-shadow:0 0 0 3px #727cf5;left:-55.8px;background:#fff;border-radius:50%;height:9px;width:9px;content:"";top:5px}@media (max-width: 767px){.timeline .event:after{left:-31.8px}}.rtl .timeline{border-left:0;text-align:right;border-bottom-right-radius:0;border-top-right-radius:0;border-bottom-left-radius:4px;border-top-left-radius:4px;border-right:3px solid #727cf5}.rtl .timeline .event::before{left:0;right:-170px}.rtl .timeline .event::after{left:0;right:-55.8px}

